import React from 'react';
import LegacyApp from './LegacyApp';
import PainelApp from './PainelApp';

function App() {
  const path = window.location.pathname || '';
  const hash = window.location.hash || '';
  const isPainel =
    path.startsWith('/painel') || hash.startsWith('#painel');

  if (isPainel) {
    return <PainelApp />;
  }

  return <LegacyApp />;
}

export default App;
